	onLoad() {
		let a = this.$queue.getData("isEnable")
		if (a) {
			this.isEnable = a;
		}
		let gender = this.$queue.getData(this.genderKey);
		if (gender) {
			if (gender === 1) {
				this.gender = gender;
			}
			if (gender === 2) {
				this.gender = gender;
			}
		}
		this.loadBanner("refresh");
		this.loadDataList();
		this.loadJuhuasuanlist();
		this.loadCouponList();
		this.loadInfos();
		this.loadMessage();
	},
	onReachBottom() {
		this.loadCouponList();
	},
	onPullDownRefresh() {
		this.page = 1;
		this.loadCouponList("Refresh");
	},
	onReady() {
		this.loadJuhuasuanlist();
	},
	onShow() {



		this.getUserInfo()
	},

	methods: {
		/**
		 * 加载首页新闻
		 */
		loadBanner: function(Refresh) {
			var indexBanner = this.$SysCache.get("app_index_banner");
			if (indexBanner && Refresh == undefined) {
				this.banners = indexBanner;
			} else {
				this.$Request.post(this.$Api.home.banners).then(res => {
					if (res.Code === 1) {
						let bannerList = res.Message;
						bannerList.forEach(item => {
							item.DocumentPictureUrl = this.$Url + item.DocumentPictureUrl;
						});
						this.banners = bannerList;
						this.$SysCache.put("app_index_banner", res.Message, 43200);
					}
				})
			}
		},
		loadDataList: function() {
			this.$Request.get('/api/get_deserve_item/apikey/maxd').then(res => {
				for (let i = this.index; i < 8; i++) {
					this.dataList.push(res.item_info[i]);
				}
			});
		},
		loadJuhuasuanlist: function() {
			this.$Request.get('/api/column/apikey/maxd/type/1/back/10/min_id/' + this.min_ids + '/sort/0/cid/' + this.cat_id).then(
				res => {
					if (res.code === 1) {
						this.juhuasuanlist = [];
						for (let i = this.index; i < this.index + 8; i++) {
							this.juhuasuanlist.push(res.data[i]);
						}
						this.min_ids = res.min_id;
					}
					uni.stopPullDownRefresh(); // 停止刷新
				});
		},
		/**
		 * @param {Object} type加载
		 */
		loadCouponList: function(type) {
			console.log("----loadCouponList----");
			let materialId;
			let cat = this.category[this.TabCur].positon;
			let apiUrl = this.$Api.common.couponlist;
			if(cat === 0){
				apiUrl = this.$Api.common.couponbestlist;
				materialId = "6708"; 
			}
			console.log("----DENG-Begin---");
			console.log("----apiUrl----" + apiUrl);
			console.log("----cat----" + cat);
			console.log("----this.page----" + this.page);
			console.log("----DENG-End---");
			
			this.category[this.TabCur].loadingType = 1;
			this.$Request.get(apiUrl, {
				ColumnId: cat,
				MaterialId: materialId,
				PageNo: this.page
			}).then(res => {
				this.category[this.TabCur].loadingType = 0;
				if (res.Code === 1) {
					if (this.page === 1) {
						this.category[this.TabCur].orderList = [];
					}
					res.Message.forEach(d => {
						let grade = this.$queue.getData('grade');
						d.tkmoneys = (d.tkmoney * 0.7).toFixed(2);
						if (grade) {
							d.tkmoney = (d.tkmoney * parseFloat(grade)).toFixed(2);
						} else {
							d.tkmoney = (d.tkmoney * 0.3).toFixed(2);
						}
						d.itemsale = d.itemsale > 10000 ? '已售 ' + (d.itemsale / 10000).toFixed(1) + '万' : '已售 ' + d.itemsale;
						this.category[this.TabCur].orderList.push(d);

					});
				} else {
					this.category[this.TabCur].loadingType = 2;
				}
				if (type === 'Refresh') {
					uni.stopPullDownRefresh(); // 停止刷新
				}
				uni.hideLoading();
			});
		},
		getUserInfo() {
			let userId = this.$queue.getData('userId');
			if (userId) {
				this.$Request.getT(this.$Api.member.GetUserInfoById).then(res => {
					if (res.Code === 1) {
						this.$queue.setData('image_url', res.Message.HeadImgUrl ? this.$Url + res.Message.HeadImgUrl : '/static/img/common/logo.jpg');
						this.$queue.setData('mobile', res.Message.Mobile);
						this.isInvitation = res.Message.isInvitation;
						this.topH = 1050;
						if (res.Message.UserGroupId) {
							this.relation_id = res.Message.UserGroupId;
							this.topH = 850;
						} else {
							this.topH = 1050;
						}
						this.$queue.setData('isInvitation', res.Message.isInvitation);
						this.$queue.setData('relation', res.Message.MUserGroup.UserGroupName);
						this.$queue.setData('grade', res.Message.MUserGroup.RankId);
						this.$queue.setData('nickName', res.Message.NickName);
						this.$queue.setData('relation_id', res.Message.UserGroupId);
						this.$queue.setData('gender', parseInt(res.Message.Gender));
					}
				});
			}
		},
		/**
		 * 加载公告
		 */
		loadMessage() {
			this.$Request.getT(this.$Api.home.messagelist).then(res => {
				if (res.Code === 1) {
					this.messageList = res.Message;
				}
			});
		},
		loadInfos() {
			let relationId = this.$queue.getData('relation_id');
			if (!relationId) {
				relationId = this.$queue.getInvitation();
			}
			let that = this;
			this.$Request.getT('/wx/token').then(res => {
				uni.request({
					url: 'https://www.gomyorder.cn/qrcode?access_token=' + res,
					method: 'POST',
					header: {
						'content-type': 'application/json'
					},
					data: {
						action_name: 'QR_LIMIT_STR_SCENE',
						action_info: {
							scene: {
								scene_str: relationId
							}
						}
					},
					success: function(result) {
						that.erweima = result.data.url;
					},
					fail: function(e) {
						console.error(e);
					}
				});
			});
		},
		/**
		 * @param {Object} e tab切换
		 */
		tabSelect(e) {
			console.log("---tabSelect---");
			this.TabCur = e.currentTarget.dataset.id;
			// this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;

			this.page = 1;

		},
		//swiper 切换
		changeTab(e) {
			console.log("---changeTab---");
			this.TabCur = e.target.current;
			this.scrollLeft = (e.target.current - 1) * 50;
			let list = this.category[e.target.current].orderList;
			if (list.length == 0) {
				uni.showLoading({
					title: '加载中...'
				});
				//第一次切换tab，动画结束后需要加载数据
				if (list.loaded !== true) {
					this.loadCouponList('Refresh');
					list.loaded = true;
				}
			}
		},
		qrR(path) {
			this.$queue.setData('erweimas', path);
		},
		toCategory(son_name) {
			uni.navigateTo({
				url: '/pages/categray/search?cid=' + this.category[this.TabCur].positon + '&name=' + son_name
			});
		},
		scroll: function(e) {
			this.old.scrollTop = parseInt(e.detail.scrollTop);
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop;
			this.$nextTick(function() {
				this.scrollTop = 0;
			});
		},
		//app查询
		goSearch() {
			uni.navigateTo({
				url: '/pages/search/search?keywords=' + this.copyTklStatus
			});
			this.$refs.simpleModalTkls.hide();
			this.copyTklStatus = '';
			uni.setClipboardData({
				data: '',
				success: r => {
					uni.showToast({
						icon: 'none',
						title: '搜索中',
						duration: 1500
					});
				}
			});
		},
		//app查询弹框
		TklmaskClose: function(e) {
			this.$refs.simpleModalTkls.hide();
			this.copyTklStatus = '';
			uni.setClipboardData({
				data: '',
				success: r => {
					uni.showToast({
						icon: 'none',
						title: '已取消搜索',
						duration: 1500
					});
				}
			});
		},
		TklmaskCloses: function(e) {
			this.$refs.simpleModalTkls.hide();
		},
		//获取剪切板
		async getClipboardData() {
			let that = this;
			uni.getClipboardData({
				success: function(res) {
					that.copyTklStatus = res.data;
					if (res.data) {
						if (res.data.indexOf('http') == -1 && res.data.indexOf('&') == -1 && res.data.indexOf('￥') == -1) {
							that.$refs.simpleModalTkls.show({
								showConfirmButton: false
							});
						} else {
							that.$refs.simpleModalTkls.hide();
						}
					} else {
						that.$refs.simpleModalTkls.hide();
					}
				},
				fail: function(res) {}
			});
		},
		/**
		 * 会员授权界面
		 */
		goPublisherInfo() {
			let token = this.$queue.getData('token');
			let userId = this.$queue.getData('userId');
			if (token) {
				uni.navigateTo({
					url: '/pages/member/publisher?uid=' + userId + '&token=' + token
				});
			} else {
				this.loginS();
			}
		},
		/**
		 * 登录检测
		 */
		loginS() {















			uni.showModal({
				title: '登录提醒',
				showCancel: false,
				content: '当前账号未登录\n请登录后申请',
				success: confirmRes => {
					if (confirmRes.confirm) {
						uni.navigateTo({
							url: '/pages/public/login'
						});
					}
				}
			});

		},
		goWebView(item) {
			if (item.PageUrl !== '') {

				uni.navigateTo({
					url: '/pages/member/ping?url=' + item.PageUrl
				});




			}
		},
		/**
		 * @param {Object} type 身份选择
		 */
		choseGender(type) {
			this.$queue.setData(this.genderKey, type);
			this.gender = type;
			this.page = 1;
			this.min_id = 1;
			if (type === 1) {
				this.TabCur = 6;
				this.cat_id = 2;
			}
			if (type === 2) {
				this.TabCur = 4;
				this.cat_id = 1;
			}
			this.loadCouponList('Refresh');
			this.$refs.simpleModalTkl.hide();
		},
		showGender() {
			this.$refs.simpleModalTkl.show({
				showConfirmButton: false
			});
		},
